.report_container{
    width: 100%;
    height: 600px;
    overflow-y: scroll;
    padding-right: 6px;
}
.report_search{
    width: 100%;
    height: 9%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.report_table{
    width: 100%;
    height: 90%;
}
.rangeDate{
    display: flex;
    justify-content: center;
    align-items: center;
}
.total7{
    height: 33px;
    width: 150px;
    background-color: rgb(50, 52, 121);
    border-style: solid;
    border-width: 1px;
    color: aliceblue;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.total8{
    height: 33px;
    width: 220px;
    background-color: rgb(16, 23, 245);
    border-style: solid;
    border-width: 1px;
    color: aliceblue;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.makeFine{
    margin-bottom: 25px;
    margin-top: 15px;
}